/* font-family: 'Lato', sans-serif;
font-family: 'Fira Code', monospace; */

body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  padding: 0;
  padding-left: 100px;
  background: #17a2b8;
}
.nav-link {
  font-size: 16px;
  color: white !important;
  padding: 7px 20px !important;
}
.nav-link:hover {
  color: white !important;
  background: rgba(53, 73, 94, 0.3);
  border-radius: 5px;
}

.navbar-brand {
  font-size: 30px !important;
  text-shadow: -4px 3px 1px #5a6268;
}

.footer {
  margin-top: 50px;
  background: #17a2b8 !important;
}
.footer .text-muted {
  color: white !important;
  font-size: 14px !important;
}

button {
  background: #5a6268;
  padding: 10px 20px;
  color: white;
  border-radius: 3px;
  border: 0;
  cursor: pointer;
  border: 1px solid 5a6268;
}
button:hover {
  background: #212529;
}

/* Left menu or side menu */
.side-bar {
  background: #e9ecef;
  height: auto;
  justify-content: center;
  display: flex;
  width: 230px;
  padding: 0 5px;
}
.side-bar ul {
  /* background: rgba(23, 162, 184, 0.4); */
  /* background: #ddeedd; */
  padding: 10px;
  width: 85%;
  padding-inline-start: 0px !important;
  align-items: center;
  justify-content: center;
}
.side-bar ul li {
  font-size: 14px;
  text-align: left;
  list-style: none;
  /* width: 90%;
  padding: 5px; */
  align-items: center;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background: white;
  transition: transform 0.5s;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin-top: 5px;
  cursor: pointer;
}
.side-bar ul li span {
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 25px;
  background: #25bcff;
  color: white;
  display: inline-block;
  border-radius: 50%;
  margin-right: 5px;
  margin-left: 5px;
  font-size: 14px;
  /* transition: transform 0.5s ease-out; */
  /* transform: translateY(-0px); */
}
.side-bar ul li a {
  color: #3c556e;
  text-decoration: none;
  /* font-family: "Lucida Console", Courier, monospace; */
  font-family: Helvetica, sans-serif;
  font-size: 12px;
  /* font-weight: 600; */
  display: block;
}

.side-bar ul li a:hover {
  color: white;
}
.side-bar ul li:hover span {
  background: white;
  color: #25bcff;
  font-weight: bold;
  margin-right: 0;
  /* transform: translateX(10em) rotateZ(360deg); */
  animation: go-right 0.25s linear;
  animation-fill-mode: forwards;
}
@keyframes go-right {
  0% {
    transform: rotateZ(0deg) scale(0.5);
    border-radius: 100%;
  }
  70% {
    transform: rotateZ(270deg) scale(1.2);
    border-radius: 100%;
    /* color: tomato; */
  }
  100% {
    transform: rotateZ(360deg) scale(1);
    border-radius: 25%;
    color: tomato;
  }
}

.side-bar ul li:hover {
  transform: scale(1.1);
  z-index: 100;
  background: #2bab0d;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  color: white;
}

/* main content */
.main-content {
  text-align: left;
  min-height: 90vh;
  justify-content: center;
}
.fieldset-container {
  width: 95%;
  padding: 5px;
  border: 1px solid #c4c9cc;
  /* background: #ebf3eb; */
  background: rgba(221, 238, 221, 0.3);
  border-left: 2px solid #c4c9cc;
  border-right: 2px solid #c4c9cc;
  border-radius: 5px;
}
.fieldset-container legend {
  width: auto;
  font-size: 20px;
  text-transform: capitalize;
  font-style: italic;
  /* color: var(--purple); */
  font-family: 'Fira Code', monospace, 'Courier New', Courier;
  color: #2bab0d;
}
.fieldset-container legend .legend-border {
  /* margin-right: 10px; */
  font-family: 'Fira Code', monospace;
  color: #c4c9cc;
}
